.container {
  width: 100%;
  margin: 0 auto;
}

@media (--viewport-medium) {
  .container {
    width: calc(100% - 36px);
  }
}

@media (--viewport-large) {
  .container {
    width: calc(100% - 48px);
  }
}

@media (--viewport-huge) {
  .container {
    width: calc(100% - 64px);
    max-width: 1100px;
  }
}

.content-wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  padding: 10px 0 20px;

  @media (--viewport-medium) {
    padding: 20px 0;
  }
}

.block-content {
  width: 100%;
  background-color: $colorHeaderBg;
  margin: 0;
}

.container-spacing {
  padding: 20px;

  @media (--viewport-medium) {
    padding: 20px 35px;
  }

  @media (--viewport-large) {
    padding: 30px 60px;
    margin: 0 auto;
  }
}
